import React from 'react';

import logo from '../divided-spiral.svg';

const Header = () => (
  <div className='App-header'>
    <img src={logo} className="App-logo" alt="logo" />
    <h2 id='banner-text'>HYPNO-TIMER</h2>
  </div>
);

export default Header;



